.navbar-fixed-top + section {
  padding-top: 70px;
}

.appear {
  visibility: hidden;
}

.animated {
  -webkit-animation-duration: 1s;
  -moz-animation-duration: 1s;
  -ms-animation-duration: 1s;
  -o-animation-duration: 1s;
  animation-duration: 1s;
}

.h1 {
  font-size: 42px;
}

.device {
  border-radius: 6px !important;
  box-shadow: -15px 15px 0 rgba(0, 0, 0, 0.15);
}

.device header {
  border-radius: 5px 5px 0 0;
}

.device.phone {
  border-radius: 15px !important;
}

.device.phone header {
  border-radius: 14px 14px 0 0;
}

.device.phone footer {
  border-radius: 0 0 14px 14px;
}

.navbar {
  border: none;
  -webkit-transition: padding ease-in-out 0.2s;
  transition: padding ease-in-out 0.2s;
  background-color: #000000;
  opacity: 0.6;
}

.navbar.affix-top {
  padding: 10px 0;
}

.navbar-nav > li > a {
  font-weight: bold;
  text-transform: uppercase;
  font-size: 12px;
}

#logged-user.navbar-nav > li > a {
  font-weight: normal;
  text-transform: none;
}

.navbar-nav .dropdown-submenu .dropdown-menu {
  left: 0;
  top: 100%;
  border: none;
  background-color: #f5f5f5;
  min-width: 220px;
}

#responsive {
  background: url("/img/render-1.png") #25313e center top no-repeat;
  background-size: cover;
}

#responsive > div {
  background-color: #25313e;
  background-color: rgba(37, 49, 62, 0.95);
}

.navbar-brand img {
  margin-top: 10px;
}

/*phone*/
@media (max-width: 767px) {
  .navbar-fixed-top + section {
    padding-top: 50px;
  }
  .navbar.affix-top {
    padding: 0;
  }
  .navbar-brand {
    float: left !important;
  }
  .h1 {
    font-size: 20px;
  }
}
